  }
}

.bottom-arrow {
  padding: 22px;
  background-color: #dcf6f8;
  border: 1px solid #d7edee;
  border-radius: 4px;
}

.bottom-arrow:after {
  content: "";
  position: absolute;
  left: 6px;
  right: 0px;
  margin: 22px 31px;
  width: 0;
  height: 0;
  border-top: 15px solid #d7edee;
  border-left: 3px solid transparent;
  border-right: 17px solid transparent;
}

